// ==== General ==== 

html {
	height: 100%;
	width: 100%;
	font-size: 100%;
	-webkit-text-size-adjust: 100%;
}

body {
	height: 100%;
	width: 100%;
	min-width: 320px;
	// overflow-x: hidden;
	font-family: var(--font-second);
	font-size: calc(20rem / 16);
	line-height: 1.5;
	color: #1D1D1D;
	background-color: #EFF1F1;
	-webkit-tap-highlight-color: transparent;

	@media (min-width: $md) {
		font-size: calc(18rem / 16);
	}
}

body.lock {
	overflow: hidden;
}


.wrapper {
	// padding-top: 60px;
	display: flex;
	flex-direction: column;
	width: 100%;
	max-width: $maxWidth + px;
	// max-width: rem($maxWidth);
	min-height: 100%;
	margin: 0 auto;
	padding: 0 15px;
	// background-color: #F5F5F8;

	@media (min-width: $xl) {
		// padding-top: 80px;
		padding: 0 40px;
	}
}


.main {
	flex: 1 0 auto;
}

.footer {
	flex: 0 0 auto;
}

._over {
	z-index: 1;
	position: absolute;
	top: 0px;
	left: 0px;
	width: 100%;
	height: 100%;
}

._cover,
._contain {
	position: relative;
}

._cover video,
._contain video,
._cover iframe,
._contain iframe,
._cover img,
._contain img {
	position: absolute;
	height: 100%;
	width: 100%;
}

._cover video,
._cover iframe,
._cover img {
	object-fit: cover;
}

._contain video,
._contain iframe,
._contain img {
	object-fit: contain;
}

.ibg {
	position: relative;
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
}

.ibg>img,
.visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	padding: 0;
	border: 0;
	overflow: hidden;
	clip: rect(0 0 0 0);
}

.hidden {
	display: none !important;
}

.page {
	// padding: 15px 0px 60px 0px;

	@media (min-width: $md) {
		// padding: 30px 0px 120px 0px;
	}
}

p {
	margin-bottom: em(10);
}