.row {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -5px;

	@media (min-width: $md) {
		margin: 0 -10px;
	}
}

.row>* {
	flex: 0 0 100%;
	max-width: 100%;
	padding: 0 5px;

	@media (min-width: $md) {
		padding: 0 10px;
	}
}

.col {
	flex: 1 0 0%;
}

@media (min-width: $md) {
	.col-md-6 {
		flex: 0 0 50%;
	}
}