/* slick-Dots */

.slick-dotted.slick-slider {
  margin-bottom: 30px;
}

.slick-dots {
  // position: absolute;
  bottom: -25px;
  display: block;
  width: 100%;
  text-align: center;

  & li {
    position: relative;
    display: inline-block;
  }

  & li button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 16px;
    height: 16px;
    font-size: 0;
    line-height: 0;
    cursor: pointer;
  }

  & li button:before {
    content: "";
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background-color: #DDEEFF;
  }

  @media (pointer: fine) {
    & li button:hover:before {
      background-color: #0277DE;
    }
  }

  & li.slick-active button:before {
    background-color: #0277DE;
  }
}




.wrap-slider {
  position: relative;
}

.slider {
  margin: 0 -7px;
  // margin-left: -7px;

  @media (min-width: $md) {
    margin: 0 -10px;
  }
}

.slick-track {
  display: flex !important;
}

// если слайды обёрнуты дополнительно в пустой div:
.slick-slide {
  height: inherit !important;
}

.slide {
  display: flex;
  flex-direction: column;
  height: 100%;
  margin: 0 7px;

  @media (min-width: $md) {
    margin: 0 10px;
  }
}

.slider__control {
  // display: none;

  @media (min-width: $md) {
    display: block;
  }
}

.slider__next,
.slider__prev {
  // position: absolute;
  top: 46.5%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  width: 60px;
  height: 60px;
  font-size: 18px;
  border-radius: 50%;

  &.slick-disabled {
    background-color: #C0C5D1;
    cursor: auto;
  }

  @media (pointer: fine) {
    &:not(.slick-disabled):hover {
      background-color: #587AFF;
    }
  }
}

.slider__prev {
  left: 0px;
  transform: rotate(180deg);

  @media (min-width: 1700px) {
    left: -26px;
  }
}

.slider__next {
  right: 0px;

  @media (min-width: 1700px) {
    right: -26px;
  }
}
