// ===== footer ===== 

@if 1 {

	.footer {
		padding: 160px 20px 60px 20px;
		border-radius: 10px;
		color: #fff;
		background-color: #003253;
	}

	.container {}

	.footer__row {
		display: flex;
		flex-wrap: wrap;
		margin-bottom: 40px;
	}

	.row {}

	.footer__col-title {
		@media (min-width: $md) {
			flex: 0 0 41.666667%;
		}
	}

	.footer__title {
		margin-bottom: 10px;
		opacity: 0.5;
	}

	.footer__col-tel {

		@media (min-width: $md) {
			flex: 0 0 58.333333%;
		}
	}

	.socials {}

	.footer__tel {
		font-family: var(--font-primary);
		font-weight: 700;
		line-height: 110%;
		text-transform: uppercase;
		@include adaptive("font-size", 100, 22, 1);
		transition: .2s ease 0s;

		@media (pointer: fine) {
			&:hover {
				color: #c0c0c0;
			}
		}
	}


	.footer__col-contact {
		@media (min-width: $md) {
			flex: 0 0 41.666667%;
			order: 1;
		}
	}

	.footer__list {
		& address {
			font-style: normal;
		}

		& li {
			display: flex;
			align-items: center;

			&:not(:last-child) {
				margin-bottom: 30px;
			}

			&::before {
				display: flex;
				justify-content: center;
				align-items: center;
				font-size: 20px;
				border-radius: 10px;
				width: 60px;
				height: 60px;
				margin-right: 20px;
				background-color: #006EAD;
			}
		}

		& a {
			transition: .2s ease 0s;

			@media (pointer: fine) {
				&:hover {
					color: #c0c0c0;
				}
			}
		}
	}

	.footer__col-map {
		@media (min-width: $md) {
			flex: 0 0 58.333333%;
			order: 1;
		}

		& img {
			border-radius: 10px;
		}
	}

	.copy {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;
		margin-top: 60px;
	}

	.copy__row {
		display: flex;
		align-items: center;

		& img {
			margin-left: 10px;
		}
	}

}


// ===== up ===== 

.btn-up {
	z-index: 5;
	position: fixed;
	right: 20px;
	bottom: 20px;
	display: flex;
	justify-content: center;
	align-items: center;
	opacity: 0;
	pointer-events: none;
	width: 60px;
	height: 60px;
	color: #000;
	font-size: 20px;
	border-radius: 50%;
	border: 1px solid rgba(29, 29, 29, 0.2);
	background-color: #fff;
	transform: rotate(-90deg);
	transition: .2s ease 0s;

	&.show {
		opacity: 1;
		pointer-events: visible;
	}
}