:root {
  --font-primary: "Roboto", sans-serif;
  --font-second: "OpenSans", sans-serif;
}


@function rem($px) {
	// 	$result: math.div($px, 16) + rem;
	$result: ($px / 16) + rem;
	@return $result;
}

@function em($px, $px-rel: 16) {
	$result: ($px / $px-rel) + em;
	@return $result;
}

@function prc($pxOne, $pxTwo) {
	$result: ($pxOne / $pxTwo) * 100%;
	@return $result;
}



$minWidth: 320;
$maxWidth: 1920;
$maxWidthContainer: 1660; 


