// fullscreen 
// ===== hero ===== 

@if 1 {

  .hero {
    flex-shrink: 0;
    position: relative;
    padding: 10px 0;
  }

  .hero__link {
    position: relative;
    bottom: 30px;
    border-radius: 30px;
    text-transform: none;
    font-weight: 500;
  }

  .parallax-window {
    padding: 40px;
    // min-height: 770px;
    aspect-ratio: 1/0.42;
    background: transparent;
  }

  .parallax-mirror {
    border-radius: 10px;
  }


  .contact {
    position: sticky;
    top: 40px;
    max-width: 530px;
    margin-left: auto;
    padding: 30px;
    font-family: var(--font-primary);
    border-radius: 10px;
    background-color: #fff;

    @media (min-width: $lg) {
      padding: 60px;
    }
  }

  .contact__text {
    margin-bottom: 30px;
    line-height: 1.3;
    font-size: calc(28rem / 16);
    font-weight: 500;

    & span {
      color: #006EAD;
    }

    @media (min-width: $md) {
      font-size: calc(36rem / 16);
    }
  }

}



// ===== buns ===== 

@if 1 {
  .buns {}

  .title__row {
    margin-bottom: 40px;

    @media (min-width: $md) {
      margin-bottom: 60px;
    }
  }

  .subtitle {
    font-family: var(--font-family);
    font-size: calc(20rem / 16);
  }

  .buns__title {
    span {
      color: #006EAD;
    }
  }

  .subtitle__col {
    @media (min-width: $xl) {
      flex: 0 0 41.666667%;
    }
  }

  .title__col {
    flex: 1 0 0%;
  }


  .buns__col {
    margin-bottom: 20px;

    @media (min-width: $md) {
      flex: 0 0 50%;
    }

    @media (min-width: $lg) {
      flex: 0 0 25%;
    }
  }

  .buns__item {
    height: 100%;
    padding: 20px;
    background-color: #fff;
    border-radius: 10px;

    @media (min-width: $xl) {
      padding: 40px;
    }

    @media (pointer: fine) {
      &:hover .buns__icon {
        background-color: #003253;
        transform: scale(1.04);
      }
    }
  }

  .buns__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    @include adaptive("margin-bottom", 80, 40, 1);
    color: #fff;
    font-size: 27px;
    border-radius: 10px;
    width: 80px;
    height: 80px;
    background-color: #006EAD;
    transition: .2s ease 0s;
  }

  .buns__item-title {
    font-family: var(--font-primary);
    font-weight: 500;
    font-size: 36px;
    line-height: 130%;
    margin-bottom: 10px;
  }
}



// ===== service ===== 

@if 1 {
  .service {
    @include adaptive("padding-left", 90, 20, 1);
    @include adaptive("padding-right", 90, 20, 1);
    @include adaptive("padding-top", 160, 40, 1);
    @include adaptive("padding-bottom", 160, 40, 1);
    color: #fff;
    background-color: #003253;
    border-radius: 10px;

    & p {
      opacity: 0.5;
    }
  }

  .review__img {
    margin-bottom: 20px;

    & img {
      border-radius: 10px;
    }
  }

  .service__col-title {
    align-self: start;

    @media (min-width: $md) {
      flex: 0 0 33.333333%;
      position: sticky;
      top: 20px;
    }
  }

  .service__subtitle {
    margin-bottom: 30px;
    opacity: 0.5;
  }

  .service__title {
    & span {
      color: #4C90CD;
    }
  }

  .service__col {
    @media (min-width: $md) {
      flex: 0 0 66.66667%;
    }

    @media (min-width: $xl) {
      flex: 0 0 58.333333%;
      margin-left: 8.333333%;
    }

    & ol {
      counter-reset: num;
      list-style: none;

      li {
        position: relative;
        padding-left: 50px;
      }

      li::before {
        content: counter(num, decimal-leading-zero)" /";
        counter-increment: num;
        position: absolute;
        left: 0;
        top: -3px;
        opacity: 0.5;

      }
    }

  }

  .service__item {
    @media (min-width: $md) {
      display: flex;
    }

    transition: .2s ease 0s;

    &:not(:last-child) {
      @include adaptive("margin-bottom", 80, 20, 1);
    }

    @media (pointer: fine) {
      .service__btn {
        opacity: 0;
      }

      &:hover .service__img img {
        border-radius: 10px;
      }

      &:hover .service__btn {
        opacity: 1;
      }
    }

    & p {
      margin-bottom: 20px;
    }
  }

  .service__img {
    flex-shrink: 0;

    @media (min-width: $md) {
      flex: 0 0 33.333333%;
    }

    & img {
      // width: 180px;
      // height: 180px;
      border-radius: 50%;
      aspect-ratio: 1/1;
      transition: .2s ease 0s;
      object-fit: cover;

      @media (min-width: $xl) {
        // width: 280px;
        // height: 280px;
      }
    }
  }

  .service__info {
    padding: 20px;

    @media (min-width: 1400px) {
      padding: 20px 20px 20px 60px;
    }
  }

  .service__item-title {
    margin-bottom: 5px;
  }

  .service__cost {
    display: inline-flex;
    border-radius: 30px;
    padding: 18px 24px;
    height: 60px;
    background-color: rgba(255, 255, 255, 0.1);
  }

  .service__btn {
    background-color: #3973E1;
    border-radius: 30px;
    transition: .2s ease 0s;
    transition: .2s ease 0s;

    @media (pointer: fine) {
      &:hover {
        background-color: #2f60ba;
      }
    }
  }

}



// ===== review ===== 

@if 1 {
  .review__img {
    background: transparent;
    aspect-ratio: 1/0.42;
  }

  .review__col-title {
    @media (min-width: $md) {
      flex: 0 0 33.333333%;
    }

    @media (min-width: $xl) {
      flex: 0 0 25%;
    }
  }

  .review__control {
    display: flex;
    flex-direction: column;
    height: 100%;
    padding: 20px;
    border-radius: 10px;
    background-color: #fff;

    @media (min-width: $xxl) {
      padding: 40px;
    }

    @media (min-width: 1700px) {
      padding: 60px;
    }
  }

  .review__title {
    font-weight: 700;
    line-height: 100%;
    text-transform: uppercase;
  }

  .slider__control {
    margin-top: auto;
  }


  .paging {
    font-family: var(--font-primary);
    font-weight: 500;
    margin-left: 10px;

    @media (min-width: $lg) {
      margin-left: 30px;
    }
  }

  .review__wrap-slider {
    height: 100%;
    padding: 20px;
    border-radius: 10px;
    background-color: #fff;

    @media (min-width: $xxl) {
      padding: 40px;
    }

    @media (min-width: 1700px) {
      padding: 60px;
    }
  }

  .review__col-slider {
    @media (min-width: $md) {
      flex: 0 0 66.66667%;
      max-width: 66.66667%;
    }

    @media (min-width: $xl) {
      flex: 0 0 75%;
      max-width: 75%;
    }
  }

  .slide__row {
    justify-content: space-between;
    align-items: center;
    margin-top: 40px;
  }

  .review__col-name {
    flex: 0 0 auto;
  }

  .review__slide-title {
    font-family: var(--font-primary);
    font-weight: 500;
    font-size: 36px;
  }

  .review__slide-name {
    font-weight: 500;
    font-family: var(--font-primary);
  }

  .review__slide-city {
    font-size: calc(16rem / 16);
  }

  .review__slide-car {
    flex: 0 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 30px;
    padding: 5px 24px;
    height: 60px;
    font-family: var(--font-primary);
    font-weight: 500;
    background-color: #EFF1F1;

    & span {
      color: #006EAD;
    }
  }
}



// ===== faq ===== 

@if 1 {

  .faq {
    text-align: center;
    color: #fff;
    border-radius: 10px;
    background-color: #003253;
    @include adaptive("padding-top", 160, 40, 1);
    @include adaptive("padding-bottom", 160, 40, 1);
  }

  .spoiler {
    position: relative;
    counter-reset: num1;
    list-style: none;
    padding: 0px 10px 0px 10px;

    &::after {
      content: "";
      position: absolute;
      left: 15px;
      right: 15px;
      top: 0;
      height: 1px;
      background-color: rgba(#fff, .6);
      display: block;
    }
  }

  .spoiler__item {
    position: relative;
    transition: .4s ease 0s;
    border-radius: 10px;

    &::before {
      content: counter(num1, decimal-leading-zero)" /";
      counter-increment: num1;
      position: absolute;
      top: 45px;
      left: 10px;
    }

    &::after {
      content: "";
      position: absolute;
      left: 5px;
      right: 5px;
      bottom: 0;
      height: 1px;
      background-color: rgba(#fff, .6);
      display: block;
    }

    &.active {
      color: #003253;
      background-color: #EFF1F1;
    }

    @media (pointer: fine) {
      &:hover {
        color: #003253;
        background-color: #EFF1F1;
      }
    }
  }



  .spoiler__btn {
    position: relative;
    width: 100%;
    padding: 40px 40px 40px 50px;
    font-weight: 500;
    @include adaptive("font-size", 36, 22, 1);
    font-family: var(--font-primary);

    &::before {
      position: absolute;
      right: 5px;
      top: 52px;
      font-size: 20px;
      transition: .4s ease 0s;
    }

    &.active {
      color: #006EAD;

      &::before {
        transform: rotateX(180deg);
        transform-origin: top;
      }
    }
  }

  .spoiler__panel {
    display: none;
    max-width: 1100px;
    margin: 0 auto;
    padding: 0px 40px 40px 50px;
  }
}



// ===== report ===== 

@if 1 {
  .report {

    // overflow: hidden;
  }

  .subtitle__col {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .report__wrap-slider {
    padding-bottom: 60px;
    margin-right: -5px;
    margin-left: -5px;

    @media (min-width: $xl) {
      margin-right: -30px;
      margin-left: -30px;
    }
  }

  .report__slider {

    & .slick-slide {
      transition: .4s ease 0s;
    }

    & .slick-center {
      width: 80vw !important;

      @media (min-width: $md) {
        width: 45vw !important;
      }

      @media (min-width: $xl) {
        width: 30vw !important;
      }
    }
  }

  .report__slider-control {
    // position: absolute;
    bottom: 0;
    left: 0;
    display: inline-flex;
  }

  .report__col-btn {
    display: flex;
    justify-content: center;
  }

  .report__btn {
    min-width: 25vw;
    border-radius: 30px;
    min-height: 60px;
  }
}