// ===== socials ===== 

.socials {
  display: flex;
  // justify-content: center;

  & li+li {
    margin-left: 10px;
  }

  & a {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60px;
    height: 60px;
    font-size: calc(24rem / 16);
    color: #fff;
    border-radius: 50%;
    // border: 1px solid #fff;
    transition: .2s ease 0s;
  }

  @media (pointer: fine) {
    & a:hover {
      // color: $color-hover-1;
      background-color: #003253 !important;
    }
  }

  & svg {
    display: block;
    width: 18px;
    height: 18px;
  }
}